.main {
    background-image: url("/images/minecraft_dirt_bg.webp");
    font-family: 'minecraft', sans-serif;
    height: 100%;
    margin : 0;
    padding: 0%;
    justify-content: center;
    align-items: center;
    display: flex;
}


.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
}


.form {
    padding: 2rem 3rem;
    border-radius: 12px;
    width: 400px;
    display: flex;
    flex-direction: column;
    gap : 1rem;
    align-items: center;
    /* align-items: center; */
}


.title {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: center;
}

.input {
  background: black;
  color: white;
  border: 2px solid white;
  padding: 0.5rem;
  height: 40px;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 3px rgba(0, 112, 243, 0.3);
}

.buttons {
  padding-top: 2rem;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

.loginButton,
.createButton {
  /* flex: 1; */
  padding: 0.3rem;
  border: black;
  border: 1px solid rgb(28, 28, 28);
  width: 300px;
  height: 40px;
  cursor: pointer;
  font-weight: 500;
}

.loginButton {
  /* background-color: #0070f3; */
  background-image: url("/images/minecraft_stone_bg.jpg");
  color: white;
}

.loginButton:hover {
  background-color: #005ad1;
}

.createButton {
  background-image: url("/images/minecraft_stone_bg.jpg");
  color: white;
}

.createButton:hover {
  background-color: #ccc;
}

.recoverLink {
  display: block;
  text-align: center;
  font-size: 0.9rem;
  color: #0070f3;
  text-decoration: none;
  margin-top: 0.5rem;
}

.recoverLink:hover {
  text-decoration: underline;
}

